import React from "react";

const Infoweather = (props) => {

    
            return <div id="today">
                <div className="btn-grados"> 
                    <div onClick={props.tocelsius} className="celsius">°C</div>
                    <div onClick={props.tofahren}  className="fahren">°F</div>
                </div>
                <div className="clearfix"></div>
                <div className="description"> {props.description}</div>
                
                <img src={`icons/${props.icon}.png`} alt=" " />
                
                <div className="temp"> {props.temp} °C </div>

               
               
                <br/>
                <div className="details">
                    <div className="humidity">Humidity: {props.humidity} %</div>
                    <div className="windspeed">Wind: {props.wind_speed}  mps </div>
                    <div className="pressure">Pressure: {props.pressure} hpa </div>
                    <div className="feelslike">Feels Like: {props.feels_like} °C </div>
                </div>
                <div className="city"> <i id="location-icon"className="fas fa-map-marker-alt"></i>{props.city}</div>
               <div id="map"></div>
                
            </div>
}

export default Infoweather;