import React, { Component } from "react";


class Task extends Component {

  onfocus = () => {
    const contenedorinput = document.querySelector(".contain-input");
    contenedorinput.style.boxShadow = "0px 0px 3px 3px rgb(1, 184, 240)";
    contenedorinput.style.border = "none";
  }

  onblur = () => {
    const contenedorinput = document.querySelector(".contain-input");
    contenedorinput.style.border = "1px solid #E7E7EB";
    contenedorinput.style.boxShadow = "none";
  }

  render() {

    return <div className="div-container">
        <form onSubmit={this.props.getinfo} className="form">
          <div className="contain-input">
            <i id="icono" name="ciudad" className="fas fa-search"></i>
            <input onBlur={this.onblur} onFocus={this.onfocus} type="text" id="nameCity" placeholder=" Ingresar ciudad" />
          </div>
            <button type="submit" id="button-search" >Buscar</button>
        </form>
    </div>
  }
}
export default Task;
