
import React, { Component } from "react";

import "./assets/App.css";
//Componentes
import Task from "./components/Task";
import Infoweather from "./components/Infoweather";

import mapboxgl from '!mapbox-gl'; // eslint-disable-line import/no-webpack-loader-syntax
 
mapboxgl.accessToken = 'pk.eyJ1IjoiZmVybmFuZG8xMGRldiIsImEiOiJja3BxNTRrYjQzMWpxMndtd3VxNmIxeGpzIn0.tegh8Ku_APJ6nDWRpAUmGQ';








class App extends Component {

  state={
    city:"",
    lat:"",
    coord:"",
    country:"",
    wind_speed:"",
    description:"",
    temp:"",
    temp_min:"",
    temp_max:"",
    humidity:"",
    feels_like:"",
    pressure:"",
    error:null,
    icon:""
  }
   
    

  getinfo = async (e) => {

    e.preventDefault();
    const {nameCity} = e.target.elements;
    const valorCiudad = nameCity.value;

    const API_URL = `https://api.openweathermap.org/data/2.5/weather?q=${valorCiudad}&units=metric&appid=1d022a1dbee6ae62dcc61d909a95c976`;

    const resp = await fetch(API_URL)
    const datos = await resp.json();
    
    this.setState({
      temp:parseInt(datos.main.temp),
      
      coord:datos.coord,
      temp_min:parseInt(datos.main.temp_min),
      temp_max:parseInt(datos.main.temp_max),
      description:datos.weather[0].description,
      humidity:datos.main.humidity,
      feels_like:parseInt(datos.main.feels_like),
      pressure:datos.main.pressure,
      wind_speed:datos.wind.speed,
      city:datos.name,
      country:datos.sys.country,
      error:null,
      icon:datos.weather[0].icon 
    })

   const today =document.querySelector("#today");
   today.style.display="block";
   today.style.animation= "fadein 500ms ease";
  
   
   

    if(today.style.display==="block"){
        
        var map = new mapboxgl.Map({
        container: 'map',
        style: 'mapbox://styles/mapbox/streets-v11',
        zoom:8,
        center:this.state.coord
        });

        map.addControl(new mapboxgl.NavigationControl());
    }  

  }

  tofahren = () => {

    document.querySelector(".temp").innerHTML= parseInt(this.state.temp*9/5+32) + " °F";
    document.querySelector(".feelslike").innerHTML= "Feels Like: " + parseInt(this.state.feels_like*9/5+32) + " °F";

  }

  tocelsius = () =>{
    document.querySelector(".temp").innerHTML= parseInt(this.state.temp)+ " °C";
    document.querySelector(".feelslike").innerHTML= "Feels Like: " + parseInt(this.state.feels_like)+ " °C";

    
  }

  
 
  render() {
    return <div>
      <h1>GetWeather <i className="fas fa-cloud"></i></h1> 
      <Task getinfo={this.getinfo} />
      <Infoweather 
        tofahren={this.tofahren}
        tocelsius={this.tocelsius}
        {...this.state} />
      <div className="social">
        <ul>
          <li ><a href=" #" className="fab fa-facebook-f"> </a> </li>
          <li><a href=" #" className="fab fa-twitter"> </a> </li>
          <li><a href=" #" className="fab fa-instagram"> </a> </li>
          <li><a href=" #" className="far fa-envelope"> </a> </li>
        </ul>
      </div>
     </div>
    }
}
export default App ;